import * as React from "react"
import * as ToastPrimitives from "@radix-ui/react-toast"
import { cva } from "class-variance-authority"
import { X, CheckCircle2, XCircle, Info, AlertTriangle, Clock, Bell } from "lucide-react"
import { cn } from "../../lib/utils"
import { useToast } from "../../hooks/use-toast"

const ToastProvider = ToastPrimitives.Provider

const ToastViewport = React.forwardRef(({ className, ...props }, ref) => (
  <ToastPrimitives.Viewport
    ref={ref}
    className={cn(
      "fixed top-4 right-4 z-[100] flex max-h-screen w-full flex-col-reverse p-4 sm:bottom-4 sm:right-4 sm:top-auto sm:flex-col md:max-w-[420px]",
      className
    )}
    {...props}
  />
))
ToastViewport.displayName = ToastPrimitives.Viewport.displayName

// Enhanced icon mapping for variants with better visual hierarchy
const variantIcons = {
  default: <Info className="h-5 w-5 text-blue-600 dark:text-blue-400" />,
  success: <CheckCircle2 className="h-5 w-5 text-emerald-600 dark:text-emerald-400" />,
  error: <XCircle className="h-5 w-5 text-red-600 dark:text-red-400" />,
  warning: <AlertTriangle className="h-5 w-5 text-amber-600 dark:text-amber-400" />,
  destructive: <XCircle className="h-5 w-5 text-red-600 dark:text-red-400" />,
  info: <Info className="h-5 w-5 text-blue-600 dark:text-blue-400" />,
  notification: <Bell className="h-5 w-5 text-purple-600 dark:text-purple-400" />,
};

// Enhanced toast variants with better visual design
const toastVariants = cva(
  "group pointer-events-auto relative flex w-full items-start space-x-4 overflow-hidden rounded-xl border p-6 pr-8 shadow-2xl transition-all duration-300 ease-in-out bg-white dark:bg-gray-900 border-gray-200 dark:border-gray-700 data-[swipe=cancel]:translate-x-0 data-[swipe=end]:translate-x-[var(--radix-toast-swipe-end-x)] data-[swipe=move]:translate-x-[var(--radix-toast-swipe-move-x)] data-[swipe=move]:transition-none data-[state=open]:animate-in data-[state=closed]:animate-out data-[swipe=end]:animate-out data-[state=closed]:fade-out-80 data-[state=closed]:slide-out-to-right-full data-[state=open]:slide-in-from-top-full data-[state=open]:sm:slide-in-from-bottom-full hover:shadow-3xl",
  {
    variants: {
      variant: {
        default: "border-blue-200 dark:border-blue-800 bg-blue-50 dark:bg-blue-950/30",
        success: "border-emerald-200 dark:border-emerald-800 bg-emerald-50 dark:bg-emerald-950/30",
        error: "border-red-200 dark:border-red-800 bg-red-50 dark:bg-red-950/30",
        warning: "border-amber-200 dark:border-amber-800 bg-amber-50 dark:bg-amber-950/30",
        destructive: "border-red-200 dark:border-red-800 bg-red-50 dark:bg-red-950/30",
        info: "border-blue-200 dark:border-blue-800 bg-blue-50 dark:bg-blue-950/30",
        notification: "border-purple-200 dark:border-purple-800 bg-purple-50 dark:bg-purple-950/30",
      },
    },
    defaultVariants: {
      variant: "default",
    },
  }
)

const Toast = React.forwardRef(({ className, variant, ...props }, ref) => {
  return (
    <ToastPrimitives.Root
      ref={ref}
      className={cn(toastVariants({ variant }), className)}
      {...props}
    />
  )
})
Toast.displayName = ToastPrimitives.Root.displayName

const ToastAction = React.forwardRef(({ className, ...props }, ref) => (
  <ToastPrimitives.Action
    ref={ref}
    className={cn(
      "inline-flex h-8 shrink-0 items-center justify-center rounded-md border bg-transparent px-3 text-sm font-medium ring-offset-background transition-colors hover:bg-secondary focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 group-[.destructive]:border-muted/40 group-[.destructive]:hover:border-destructive/30 group-[.destructive]:hover:bg-destructive group-[.destructive]:hover:text-destructive-foreground group-[.destructive]:focus:ring-destructive",
      className
    )}
    {...props}
  />
))
ToastAction.displayName = ToastPrimitives.Action.displayName

const ToastClose = React.forwardRef(({ className, ...props }, ref) => (
  <ToastPrimitives.Close
    ref={ref}
    className={cn(
      "absolute right-2 top-2 rounded-md p-1.5 text-foreground/50 opacity-0 transition-all duration-200 hover:text-foreground hover:bg-gray-100 dark:hover:bg-gray-800 focus:opacity-100 focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 group-hover:opacity-100 group-[.destructive]:text-red-300 group-[.destructive]:hover:text-red-50 group-[.destructive]:hover:bg-red-100 group-[.destructive]:focus:ring-red-400 group-[.destructive]:focus:ring-offset-red-600",
      className
    )}
    toast-close=""
    {...props}
  >
    <X className="h-4 w-4" />
  </ToastPrimitives.Close>
))
ToastClose.displayName = ToastPrimitives.Close.displayName

const ToastTitle = React.forwardRef(({ className, ...props }, ref) => (
  <ToastPrimitives.Title
    ref={ref}
    className={cn("text-sm font-semibold text-gray-900 dark:text-gray-100", className)}
    {...props}
  />
))
ToastTitle.displayName = ToastPrimitives.Title.displayName

const ToastDescription = React.forwardRef(({ className, ...props }, ref) => (
  <ToastPrimitives.Description
    ref={ref}
    className={cn("text-sm text-gray-700 dark:text-gray-300 leading-relaxed", className)}
    {...props}
  />
))
ToastDescription.displayName = ToastPrimitives.Description.displayName

// Enhanced progress bar component with better visual design
const ToastProgress = ({ duration = 4000, variant = "default" }) => {
  const [width, setWidth] = React.useState("100%");
  
  React.useEffect(() => {
    const timer = setTimeout(() => {
      setWidth("0%");
    }, 100);
    
    return () => clearTimeout(timer);
  }, []);

  const getProgressColor = (variant) => {
    switch (variant) {
      case 'success':
        return 'bg-emerald-500 dark:bg-emerald-400';
      case 'error':
      case 'destructive':
        return 'bg-red-500 dark:bg-red-400';
      case 'warning':
        return 'bg-amber-500 dark:bg-amber-400';
      case 'info':
      case 'notification':
        return 'bg-blue-500 dark:bg-blue-400';
      default:
        return 'bg-blue-500 dark:bg-blue-400';
    }
  };

  return (
    <div className="absolute left-0 bottom-0 h-1 w-full bg-gray-200 dark:bg-gray-700 rounded-b-xl overflow-hidden">
      <div
        className={`h-1 ${getProgressColor(variant)} transition-all duration-[4000ms] ease-linear rounded-b-xl`}
        style={{ width, transitionDuration: `${duration}ms` }}
      />
    </div>
  );
};

// Enhanced toast component with better layout and accessibility
const EnhancedToast = ({ id, title, description, action, variant = "default", duration = 4000, ...props }) => {
  const [isVisible, setIsVisible] = React.useState(false);
  
  React.useEffect(() => {
    const timer = setTimeout(() => setIsVisible(true), 50);
    return () => clearTimeout(timer);
  }, []);

  return (
    <Toast 
      key={id} 
      variant={variant} 
      className={cn(
        "transform transition-all duration-300 ease-out",
        isVisible ? "translate-x-0 opacity-100 scale-100" : "translate-x-full opacity-0 scale-95"
      )}
      {...props}
    >
      <div className="flex items-start gap-4 w-full">
        {/* Icon with better visual hierarchy */}
        <div className="flex-shrink-0 mt-0.5">
          <div className="p-1.5 rounded-lg bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm">
            {variantIcons[variant] || variantIcons.default}
          </div>
        </div>
        
        {/* Content area */}
        <div className="grid gap-1 flex-1 min-w-0">
          {title && (
            <ToastTitle className="text-base font-semibold">
              {title}
            </ToastTitle>
          )}
          {description && (
            <ToastDescription className="text-sm leading-relaxed">
              {description}
            </ToastDescription>
          )}
        </div>
        
        {/* Action buttons */}
        {action && (
          <div className="flex-shrink-0">
            {action}
          </div>
        )}
        
        {/* Close button */}
        <ToastClose className="opacity-100 hover:scale-110 transition-transform duration-200" />
      </div>
      
      {/* Enhanced progress bar */}
      <ToastProgress duration={duration} variant={variant} />
    </Toast>
  );
};

// Toast component props
export const toastProps = {
  variant: {
    default: 'default',
    success: 'success',
    error: 'error',
    warning: 'warning',
    destructive: 'destructive',
    info: 'info',
    notification: 'notification'
  }
}

export {
  ToastProvider,
  ToastViewport,
  Toast,
  ToastTitle,
  ToastDescription,
  ToastClose,
  ToastAction,
}

// Main Toaster component with enhanced features
export function Toaster() {
  const { toasts } = useToast()

  return (
    <ToastProvider>
      {toasts.map(({ id, title, description, action, variant = "default", duration = 4000, ...props }) => {
        return (
          <EnhancedToast
            key={id}
            id={id}
            title={title}
            description={description}
            action={action}
            variant={variant}
            duration={duration}
            {...props}
          />
        )
      })}
      <ToastViewport />
    </ToastProvider>
  )
}
